






import Vue from 'vue';

export default Vue.extend({
  props: {
    htmlType: {
      type: String as () => 'submit' | 'button',
      default: 'button',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    classList(): Array<string> {
      return ['app-button', this.disabled ? 'app-button--disabled' : ''];
    },
  },
});
